html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}
body {
	line-height: 1;
}
ol,
ul {
	list-style: none;
}
blockquote,
q {
	quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
	content: "";
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

@font-face {
	font-family: "coder";
	src: local("coder"), url(../assets/fonts/pixel.ttf) format("truetype");
}

body {
	margin: 0;
	font-family: "coder", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

body::-webkit-scrollbar {
	display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
body {
	-ms-overflow-style: none; /* IE and Edge */
	scrollbar-width: none; /* Firefox */
}

html {
	background-color: "#4C4C4C";
}

div {
	-moz-user-select: none;
	-webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;
	-o-user-select: none;
}

@font-face {
	font-family: 'pixel';
	src: url('../assets/fonts/pixel.ttf') format('truetype');
}

* {
	margin: 0;
	padding: 0;
}

html {
	scroll-behavior: smooth;
	max-width: 100%;
	overflow-x: hidden;
}
body {
	background: #444;
	max-width: 100%;
	overflow-x: hidden;
	font-family: 'sans-serif';
}

a {
	text-decoration: none;
	color: #fff;
	font-weight: 700;
}

nav {
	background: rgba(93, 93, 93, 0.1);
	padding: 0px;
	color: white;
	position: absolute;
	display: flex;
	justify-content: center;
	padding-right: 30px;
	align-items: center;
	width: 100%;
	transition: all 0.6s linear;
}
nav > a {
	list-style: none;
	display: flex;
	margin: 1em 2em;
	justify-content: center;
	align-items: center;
	transition: all 0.1s linear;
}
nav > #nav-button {
	margin: 10px;
}
nav > a:hover {
	color: white;
	transition: all 0.1s linear;
	border-bottom: 0px solid white;
}

nav > a:hover {
	/* border-top: 1px solid black; */

	border-bottom: 3px solid white;
	transition: all 0.1s linear;
}
.sticky {
	position: fixed;
	top: 0;
	width: 100%;
	background: #333;
	transition: all 0.6s linear;
}

section {
	padding: 40px 80px;
	background: #456f34;
}

.heading {
	display: grid;
	grid-template-columns: 1fr 1fr;
	justify-content: center;
	align-items: center;
	background: darkgreen;
	height: 695px;
	background-image: url("../assets/images/Forest2.png");
}

.heading img {
	max-width: 250px;
}
.heading-sub {
	display: flex;
	flex-flow: column;
	justify-content: center;
	align-items: center;
}
h1 {
	/* font-family: "GothicPixels"; */
	font-family: pixel;
	font-size: 80px;
	color: #e3e3e3;
	letter-spacing: 2px;
	text-shadow: 1px 1px 3px black;
	/* padding: 16px; */
}

h2 {
	margin: 16px 16px 4px 0px;
	font-family: pixel;
	font-size: 40px;
	color: #e3e3e3;
	letter-spacing: 2px;
	text-shadow: 1px 1px 3px black;
	padding: 40px 40px 12px 40px;
}
button {
	margin: 12px;
	padding: 20px;
	border-radius: 5px;
	border: none;
	background: orangered;
	color: white;
	text-shadow: 1px 1px 1px black;
	font-family: pixel;
	box-shadow: 1px 1px 8px black;
}
button:hover {
	cursor: pointer;
}
.nav-button,
.mint-button {
	/* display: none; */
	opacity: 0;
	display: none;
	/* margin-right: 30px; */
	margin: 0;
	height: 0px;
	font-size: 0px;
	width: 0px;
	height: 0px;
	transition: all 0.6s linear;
}
.nav-button:hover {
	cursor: pointer;
}
#nav-button {
}

#nav-button:hover {
	border: none;
	cursor: pointer;
}

.sticky-button {
	opacity: 1;
	display: block;
	padding: 10px;
	height: 56px;
	margin: 8px;
	font-size: 12px;
	width: 160px;
	transition: all 0.6s linear;
}

/* #character {
    flex-flow: row;
	justify-content: flex-end;
	align-items: flex-end;
} */

.intro {
	/* height: 400px; */
	background: #504136;
	/* height: 400px; */
	background-image: url("../assets/images/ground-export.png");
	background-position: left;
	padding: 40px 240px;
	/* background-repeat: no-repeat; */
	/* background-size: cover; */
}

.intro,
.description,
.rares,
.faqs {
	padding: 40px 240px;
}

.container {
	display: none;
}

.gallery-container {
	display: none;
}
.big-gallery {
	display: block;
	padding: 80px 0px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
}
.big-gallery > img {
	max-width: 250px;
	border: 1px solid black;
	border-radius: 20px;
	margin: 12px;
}

.rares {
	background: #584639;
	/* background: #228b22; */
	/* background-image: url("./img/ground-export.png"); */
	background-position: left;
}

.description {
	/* background-color: #584639; */
}
.faqs {
	/* background-color: #584639; */
}

footer {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	justify-content: center;
	align-items: center;
	text-align: center;
	margin: auto;
	background: #111;
	min-height: 300px;
	padding: 40px;
}

footer > .heading-sub {
	justify-content: center;
	margin: auto;
	align-items: center;
	text-align: center;
}

footer > .heading-sub > p {
	padding: 0;
}

footer > #title {
	grid-column: 3;
}

footer > #character {
	grid-column: 2;
}

footer > .heading-sub > img {
	width: 250px;
}
footer > .heading-sub > h3 {
	font-family: pixel;
	font-size: 40px;
	color: #e3e3e3;
	letter-spacing: 2px;
	text-shadow: 1px 1px 3px black;
}

p {
	font-family: sans-serif;
	font-size: 20px;
	color: #ddd;
	padding: 0 40px;
	margin: 20px 0;
}

.divider {
	border-top: 3px solid rgba(0, 0, 0, 0.2);
	max-width: 600px;
	margin-left: 40px;
}

.top > ul,
.right > ul {
	padding: 0 60px;
	text-decoration: none;
	color: white;
	font-size: 18px;
}
.top > ul > li,
.right > ul > li {
	padding: 8px 0;
}

.split {
	display: grid;
	grid-template-columns: 1fr 1fr;
	justify-content: center;
	align-items: center;
}

.accordion {
	background-color: #eee;
	color: #444;
	cursor: pointer;
	padding: 18px;
	width: 100%;
	text-align: left;
	border: none;
	outline: none;
	transition: 0.4s;
	font-size: 16px;
	text-shadow: none;
}

.active,
.accordion:hover {
	background-color: #ccc;
}

.panel {
	padding: 0 18px;
	/* background-color: white; */
	max-height: 0;
	overflow: hidden;
	transition: max-height 0.2s ease-out;
}

.accordion:after {
	content: '\02795';
	font-size: 13px;
	color: #777;
	float: right;
	margin-left: 5px;
}

.active:after {
	content: "\2796";
}
#hidden-quant {
	display: none;
}

#mint-body {
	padding: 40px 240px;
}
@media only screen and (max-width: 1200px) {
	.heading {
		grid-template-columns: 1fr;
		height: 650px;
		padding: 5px;
	}

	.heading-sub > p {
		text-align: center;
	}
	.heading-sub > img {
		max-width: 175px;
		margin-bottom: 52px;
	}

	.divider {
		margin: 0;
	}

	.heading > button {
		padding: 12px;
	}
	h1 {
		font-size: 36px;
		text-align: center;
		margin-top: 40px;
	}
	h2 {
		font-size: 30px;
		text-align: center;
		margin-top: 0;
	}

	.sticky {
		transition: all 0.6s linear;
		height: auto;
	}
	nav {
		height: auto;
		z-index: 1002;
		padding-right: 0px;
	}

	nav > a {
		margin: 0;
		color: white;
	}
	nav > a {
		margin: 0;
		color: white;
	}

	nav > .nav-links {
		display: none;
	}

	#nav-button {
		display: none;
	}

	.nav-button {
		margin-right: 0px;
		margin-left: 0px;
	}
	.sticky > #nav-button {
		display: block;
		transition: all 0.5s linear;
	}

	nav > .sticky-button {
		display: block;
		transition: all 0.6s linear;
	}

	.intro,
	.description,
	.rares,
	.faqs {
		padding: 40px 0;
	}
	.big-gallery {
		display: none;
	}

	.split {
		grid-template-columns: 1fr;
	}

	.top {
		grid-row: 1;
	}

	.bottom {
		grid-row: 2;
	}

	.faqs {
		margin: 0;

		display: flex;
		flex-flow: column;
		justify-content: center;
	}

	.accordion {
		width: 95%;
	}

	footer {
		grid-template-columns: 1fr;
	}

	footer > #title {
		grid-column: 1;
	}

	footer > #character {
		grid-column: 1;
	}

	.container {
		display: flex;
		justify-content: center;
		align-items: center;
	}

	.carousel-indicators {
		display: none;
	}

	.active:after {
		display: none;
	}

	.active {
		background: transparent;
	}

	.carousel {
		max-width: 100%;
	}

	.carousel-item > img {
		max-width: 100%;
		height: auto;
	}

	.carousel-item {
		/* background: blue; */
		/* display: flex; */
		justify-content: center;
		align-items: center;
	}
	#mint-body {
		padding: 40px;
	}
}
